.feature-page{
    z-index: 2;
    position: relative;
    bottom: 13vh;
    /* background-color: green; */
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    pointer-events: none;

    /* border: 2px solid green; */
}
.features{
    display: flex;
    width: 90vw;
    align-items: center;
    justify-content: center;
    /* background-color: red; */
}

.features-title{
    /* background-color: red; */
    text-align: center;
}

.feature{
    background-color: rgba(255, 255, 255, 0.041);
    border-radius: 10px;
    color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.8vw;
    padding-bottom: 0;
    margin: 8px;

    width: 22.5vw;
}

.feature-title{
    display: flex;
    align-items: center;
    
}

.feature-text{
    margin-top: 6vh;
    margin-bottom: 20px;
    text-align: center;
    opacity: 35%;
    max-width: 90%;
    font-size: 1vw;
    font-weight: 100;
    margin-bottom: 20vh;
}

.feature-title span{
    margin-left: 0.4vw;
    font-weight: 500;
    font-size: 1.4vw;
}

.feature-img{
    width: 70%;
    position: absolute;
    top: 24vh;
}

#feat-img-2{
    width: 105%;
}

#feat-img-3{
    top: 17vh;
}

.feature-title{
    font-family: 'Inter', sans-serif;
    font-weight: 300;
    font-size: 1.2vw;
    text-align: center;
    margin-top: 1vh;
}

@media screen and (max-width: 900px) {
    .feature-page{
        bottom: 20vh;
    }
    
    .features{
        flex-direction: column;
    }
    .feature{
        width: 70vw;
        margin-bottom: 40px;
        padding: 5vw;
    }

    .feature-title{
        font-size: 4.5vw;
    }

    .feature-title span{
        font-size: 4.5vw;
        margin-left: 1.5vw;
    }

    .feature-text{
        font-size: 3vw;
        font-weight: 300;
    }

    /* 
    #feat-img-2{
        width: 65%;
    }

    #feat-img-3{
        top: 20vh;
    } */

    
}