.mockup-page{
    padding-top: 30vh;
    padding-bottom: 10vh;
    width: 100%;
    height: 100vh;
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    position: relative;
}

.mockup-content{
    height: 100%;
    /* background-color: green; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.mockup-title{
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    line-height: 120%;
    font-size: 1.5vw;
    text-align: center;
    color: white;
}

.phone{
    width: 18vw;
    height: auto;
    z-index: 1;
}

.black-phone-fade{
    width: 100%;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 42%, rgba(255,255,255,0) 100%);
    height: 45%;
    position: absolute;
    bottom: 0;
    z-index: 2;
    opacity: 0;

    animation-name: fadeIn;
    animation-delay: 0.5s;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
}

.phone-ring{
    z-index: 0;
    width: 27vw;
    height: 27vw;

    border: 1.5px solid rgba(255, 255, 255, 0.877);
    border-radius: 100%;
    position: absolute;

    opacity: 30%;
}

#ring-phone-2{
    width: 35vw;
    height: 35vw;
    opacity: 20%;
}

#ring-phone-3{
    width: 45vw;
    height: 45vw;
    opacity: 10%;
}

@media screen and (max-width: 900px) {
    .black-phone-fade{
        animation: none;
        opacity: 1;
    }
    
    .mockup-page{
        /* border: 1px solid red; */
        padding-top: 0;
        padding-bottom: 0;
    }

    .phone-ring{
        transform: scale(3.3);
        border: 0.5px solid rgba(255, 255, 255, 0.877);
    }

    .mockup-title{
        font-size: 5vw;
    }

    .phone{
        width: 80vw;
    }

    .black-phone-fade{
        height: 60%;
    }
}