.nav{
    position: fixed;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    padding-top: 4px;
    height: 40px;
    z-index: 1000;
    /* background-color: rgba(255, 0, 0, 0.863); */
    backdrop-filter: blur(10px);
}

.nav-buttons > *{
    margin: 0 25px;
    cursor: pointer;
    transition: 0.3s;
    opacity: 50%;
}

.nav-buttons > *:hover{
    /* transform: scale(1.1); */
    opacity: 100%;
}

.nav-buttons{
    font-size: 13px;
    display: flex;
    /* background-color: blue; */
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

.nav-highlight{
    position: absolute;
    right: 0;
}

.nav > .logo{
    width: 30px;
    max-height: 30px;
    position: absolute;
    left: 25px;
}

@keyframes dropIn {
    0%{
        transform: translateY(-100%);
    }
    100%{
        transform: translateY(0);
    }
}

/* Coming Soon css */
.coming-soon{
    position: fixed;
    top: 5vh;
    left: 3vw;
    z-index: 100000;
    width: 200px;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.112);
    backdrop-filter: blur(10px);
    color: white;
    font-size: 13px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    opacity: 0;

    animation-name: dropInDropOut;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes dropInDropOut {
    0%{
        transform: translateY(-100%);
        opacity: 0;
    }
    20%{
        transform: translateY(0);
        opacity: 1;
    }
    80%{
        transform: translateY(0);
        opacity: 1;
    }
    100%{
        transform: translateY(-100%);
        opacity: 0;
    }
}

/* mobile css */


@media screen and (min-width: 900px) {
    .menu-btn{
        text-align: center;
    }
}

@media screen and (max-width: 900px) {
    .mobile-nav{
        position: fixed;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: right;
        width: 100vw;
        padding-top: 4px;
        height: 40px;
        z-index: 1000;
        /* background-color: rgba(0, 0, 0, 0.107); */
        /* backdrop-filter: blur(10px); */
    }
    
    .mobile-nav > .logo{
        width: 30px;
        max-height: 30px;
        position: absolute;
        left: 25px;
    }
    
    .open-menu{
        position: relative;
        right: 25px;
        width: 25px;
        height: auto;
        cursor: pointer;
        color: white;
        background-color: rgba(255, 255, 255, 0.11);
        border-radius: 8px;
        padding: 8px;
        text-align: center;
        backdrop-filter: blur(10px);
    }
    
    .menu-container{
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        width: 60vw;
        background-color: rgba(255, 255, 255, 0.055);
        z-index: 100000;
        backdrop-filter: blur(30px);
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        padding-right: 60px;
        padding-top: 50px;
        border-radius: 20px 0 0 0;
        opacity: 0;
    }

    .menu-enter{
        animation-name: slideInRight;
        animation-duration: 0.4s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
    }

    .menu-leave{
        animation-name: slideOutRight;
        animation-duration: 0.4s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
    }

    .menu-btn{
        font-size: 25px;
        font-weight: 400;
        cursor: pointer;
        color: white;
    }

    .close-menu{
        position: absolute;
        top: 30px;
        left: 40px;
        width: 30px;
        height: auto;
        cursor: pointer;
        color: white;
    }

    .menu-highlight{
        background: -webkit-linear-gradient(30deg, #72deff, #ff71c4);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}


@keyframes slideInRight {
    0%{
        transform: translateX(40%);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideOutRight {
    0%{
        transform: translateX(0);
        opacity: 1;
    }
    100%{
        transform: translateX(40%);
        opacity: 0;
    }
}