html{
    background-color: rgb(0, 0, 0);
}

.bg {
    
    overflow: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    background-image: url("../assets/NitesOut Gradient BG.jpg");
    pointer-events: none;
}

@media screen and (min-width: 900px) {
    .bg {
        width: 100vw;
        height: 100vh;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-attachment: fixed;
    }
}

.under-landing{
    /* background-color: red; */
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pages-container{
    /* background-color: green; */
    width: 1080px;
}

.fade-away{
    animation-name: fadeAway;
    animation-duration: 0.8s;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.fade-in{
    animation-name: fadeIn;
    animation-duration: 0.8s;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

@keyframes fadeAway {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

@keyframes fadeIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.landing{
    display: flex;
    width: 100vw;
    /* background-color: red; */
    align-items: center;
    justify-content: center;
    color: white;
}

.landing-content{
    /* background-color: blue; */
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 2;
}

.landing-logo{
    width: 3vw;
    margin-bottom: -2rem;
    animation-name: fadeInUp;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.landing-title{
    font-size: 4.5vw;
    animation-name: fadeInUp;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.landing-sub{
    margin-top: -3vh;
    font-size: 0.9vw;
    font-weight: 00;
    opacity: 0;
    max-width: 40vw;

    animation-name: fadeInUp-alt-subtitle;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    animation-delay: 0.1s;
}

.landing-waitlist{
    opacity: 0;
    animation-name: fadeInUp;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    animation-delay: 0.1s;

    background-color: rgba(255, 255, 255, 0.068);
    color: rgb(255, 255, 255);
    padding: 20px 45px;
    font-size: 15px;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 30px;

    box-shadow:
    inset 0 0 50px rgba(255, 255, 255, 0.263),      /* inner white */
    inset 20px 0 80px rgba(255, 0, 255, 0.662),   /* inner left magenta short */
    inset -20px 0 80px #0ff,  /* inner right cyan short */
    inset 20px 0 300px #f0f,  /* inner left magenta broad */
    inset -20px 0 300px #0ff, /* inner right cyan broad */
    0 0 50px rgba(255, 255, 255, 0.114),            /* outer white */
    -10px 0 80px #f0f,        /* outer left magenta */
    10px 0 80px #0ff; 

    transition: 0.3s;
}

.landing-waitlist:hover{

    transform: scale(1.1);

    /* background: rgb(214,137,255); */
    /* background: linear-gradient(126deg, rgba(214,137,255,1) 0%, rgba(115,241,255,1) 100%); */


}

.bar-node{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    animation-name: fadeInUp;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInUp {
    0%{
        opacity: 0;
        transform: translateY(20px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

#bar-node-1{
    bottom: 20vh;
    left: 10vw;
    animation-delay: 0.2s;
}

#bar-node-2{
    bottom: -7vh;
    right: 23vw;
}

#bar-node-3{
    right: -6vw;
    animation-delay: 0.3s;
}

.bar-img{
    width: 11vw;
    height: auto;
    /* border-radius: 100px; */
}

.ring{
    width: 20vw;
    height: 20vw;
    border: 1.5px solid rgba(255, 255, 255, 0.877);
    border-radius: 100%;
    position: absolute;

    opacity: 50%;

    animation-name: ringPulse;
    animation-duration: 7s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

}

#ring-1-2, #ring-2-1 #ring-3-1{
    animation-delay: 2s;
}

#ring-1-3{
    animation-delay: 4s;
}

#ring-1-2{
    height: 30vw;
    width: 30vw;
    border: 1.5px solid rgba(255, 255, 255, 0.575);
}

#ring-1-3{
    height: 45vw;
    width: 45vw;
    border: 1.5px solid rgba(255, 255, 255, 0.158);
}

@keyframes ringPulse {
    0%{
        opacity: 50%;
        transform: scale(1);
    }
    50%{
        opacity: 100%;
        transform: scale(1.1);
    }
    100%{
        opacity: 50%;
        transform: scale(1);
    }
}

@media screen and (max-width: 900px) {
    .bg{
        width: 100vw;
        height: 100vh;
        background-size: 100vw 1000px;
    }
    
    .landing-logo{
        margin-bottom: -3vh;
