    }
    
    .landing-title{
        font-size: 12vw;
    }

    .landing-sub{
        margin-top: -2vh;
        font-size: 3vw;
        max-width: 80vw;
        opacity: 50%;
    }

    /* do nothing on hover */
    .landing-waitlist:hover{
        transform: scale(1);
    }

