/* Event Component CSS */
.event-card{
    width: 301px;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.034);
    border-radius: 13px;
    margin: 0 8px;
    transition: 0.3s;
    cursor: pointer;
}

.event-card:hover{
    transform: scale(1.03);
}

.event-thumbnail{
    width: 100%;
    height: 60%;

    border-top-left-radius: 13px;
    border-top-right-radius: 13px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.event-info{
    margin: 30px;
}

.event-date{
    margin-bottom: -5px;
}

.event-date, .event-location{
    opacity: 40%;
}

/* Thumbnail IDs */
#fright-night{
    background-image: url("../assets/events/frightnitenoto.jpg");
}

#bolly-blackout{
    background-image: url("../assets/events/bollywoodblackout.png");
}

#spring-spec{
    background-image: url("../assets/events/springspec.png");
}

/* Events Page CSS */
.events-page-container{
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.events-page-title{
    margin-top: 20vh;
    margin-bottom: 6vh;
    font-size: 2vw;

    font-family: 'Inter', sans-serif;
    font-weight: 400;

    background: -webkit-linear-gradient(30deg, #72deff, #ff71c4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    opacity: 0;

    animation-name: fadeInUp;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;

}

.events-page-content{
    width: 951px;

    margin-top: -6vh;

    animation-name: fadeInUp;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    animation-delay: 0.3s;

    opacity: 0;

}

.highlight-event{
    width: 100%;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.11);

    border-radius: 13px;

    background-image: url("../assets/events/EventBrite\ Banner@3x.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: 0.3s;
    cursor: pointer;
    position: relative;
}

.highlight-event:hover{
    transform: scale(1.03);
}

.highlight-event-tags{
    display: flex;
    position: absolute;
    right: 0;
    bottom: -10px;
}

.highlight-event-tags > p{
    margin: 0 10px;
    background-color: white;
    font-weight: 400;
    font-size: 14px;
    padding: 8px 18px;
    border-radius: 100px;
}

.black-text{
    color: black;
}

.cool-tag{
    background: linear-gradient(30deg, rgb(114, 222, 255), rgb(255, 113, 196));
}

.events-grid{
    display: flex;
    justify-content: space-between;
}

.timing-title{
    margin-top: 50px;
    opacity: 90%;
    margin-left: 4px;
}

@media screen and (max-width: 900px){
    .highlight-event{
        background-image: url("../assets/events/LitesOut-poster.png");
    }

    .events-page-title{
        font-size: 6vw;
    }

    .alt-subtitle{
        font-size: 3vw;
        max-width: 70vw;
    }

    .events-page-content{
        width: 95vw;
    }

    .timing-title{
        font-size: 3vw;
        margin-bottom: 15px;
    }

    .highlight-event-tags > p{
        font-size: 2vw;
    }

    .events-grid{
        align-items: center;
        flex-direction: column;
    }

    .event-card{
        width: 95vw;
        margin: 10px 0;
    }

    .event-card:hover{
        transform: scale(1);
    }
}